.App {
  display: flex;
  height: 100vh;
  overflow: hidden;
  color: #373d49;
}

.App > div {
  flex: 1;
  height: auto;
  height: 100%;
}


.App > div:last-child {
  flex: 1;
  height: auto;
  height: 100%;
  overflow: auto;
  padding: 10px;
  padding-top: 0;
}

.ReactCodeMirror > .CodeMirror {
  border: 1px solid #eee;
  height: 100vh;
  background-color: #f6f6f0;
}


table {
  border: 1px solid gainsboro;
}

table th {
  padding: 10px;
}

table tr {
  border: 1px solid gainsboro;
}

table td {
  padding: 10px;
}

blockquote {
  border-left: 10px solid #d1d7dd;
  padding-left: 5px;
}

